<template src="./Course.html"></template>
<style src="./css/course.css"></style>

<script>
export default {
  methods: {
    course() {
      unlock();
      var id = event.currentTarget.id;
      var idlist = {
        course_1: "course_in_1",
        course_2: "course_in_2",
        course_3: "course_in_3",
        course_4: "course_in_4",
      };
      var contentid = idlist[id];
      document.getElementById(id).style.background = "#1c7db2";
      document.getElementById(contentid).className = "course_in";
      document
        .getElementById(contentid)
        .setAttribute("course_in", "course_in_unlock");

      function unlock() {
        var i = 1;
        for (i = 1; i <= 4; i++) {
          contentid = "course_" + i;
          document.getElementById(contentid).style.background = "#b6b5b5";
        }
        for (i = 1; i <= 4; i++) {
          contentid = "course_in_" + i;
          document.getElementById(contentid).className = "course_in_unlock";
          document
            .getElementById(contentid)
            .setAttribute("course_in", "course_in_unlock");
        }
      }
    },
  },
};
</script>
